/* General Styling for Project Section */
.project-section {
    padding: 60px 20px; /* Space around the section */
    background-color: #f9f9f9; /* Light gray background for contrast */
  }
  
  /* Styling for Individual Slides */
  .project-slide {
    text-align: center;
    padding: 10px; /* Padding around each slide */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Soft shadow around slides */
    border-radius: 8px; /* Rounded corners for a modern design */
    background-color: #fff; /* White background for contrast */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .project-slide:hover {
    transform: translateY(-5px); /* Slight lift effect on hover */
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2); /* Enhanced shadow on hover */
  }
  
  /* Image Container Styling */
  .project-image-container {
    width: 100%;
    height: 200px;
    aspect-ratio: 16 / 9; /* Maintain aspect ratio for images */
    overflow: hidden;
    border-radius: 6px; /* Rounded corners for images */
    margin-bottom: 15px; /* Space between the image and title */
    background-color: #eaeaea; /* Light gray background for empty states */
  }
  
  .project-image-container img {
    width: 100%;
    height: 100%;
    object-fit: contain; /* Preserve image aspect ratio */
    transition: transform 0.3s ease; /* Smooth transition on hover */
  }
  
  .project-image-container img:hover {
    transform: scale(1.05); /* Slight zoom-in effect on hover */
  }
  
  /* Project Title Styling */
  .project-slide h4 {
    font-size: 1.25rem;
    font-weight: 600;
    color: #555; /* Medium gray text color */
    margin: 0;
  }
  
  /* Styling for Dots Navigation */
  .slick-dots {
    position: relative;
    bottom: -15px; /* Adjust position below the slider */
  }
  
  .slick-dots li button:before {
    font-size: 12px;
    color: #aaa; /* Default dot color */
  }
  
  .slick-dots li.slick-active button:before {
    color: #ff7f50; /* Highlighted color for active dot */
  }
  
  /* Responsive Styling */
  @media (max-width: 1024px) {
    .project-section h2 {
      font-size: 1.8rem; /* Adjust heading size for smaller screens */
    }
    .project-image-container {
      height: 180px; /* Adjust height for tablets */
    }
  }
  
  @media (max-width: 768px) {
    .project-section h2 {
      font-size: 1.6rem; /* Adjust heading size for mobile */
    }
    .project-image-container {
      height: 150px; /* Adjust height for mobile */
    }
  }
  